.img {
	transition: all 0.5s;
	width: 100%;
	border-radius: 100%;
}
.card {
	background: rgb(254, 202, 87);
	display: inline-table;
	padding: 35px;
	border-radius: 5px;
}
.name {
	font-size: 22px;
	font-weight: bold;
}

.idd {
	font-size: 14px;
	font-weight: 600;
}

.idd1 {
	font-size: 12px;
}

.number {
	font-size: 22px;
	font-weight: bold;
}

.follow {
	font-size: 12px;
	font-weight: 500;
	color: #444444;
}

.btn1 {
	height: 40px;
	width: 150px;
	border: none;
	background-color: #000;
	color: #aeaeae;
	font-size: 15px;
}

.text span {
	font-size: 13px;
	color: #545454;
	font-weight: 500;
}

.icons i {
	font-size: 19px;
}

hr .new1 {
	border: 1px solid;
}

.join {
	font-size: 14px;
	color: #a0a0a0;
	font-weight: bold;
}

.date {
	background-color: #ccc;
}
